import React, { FC } from 'react';
import styled from 'styled-components';
import { BoostedSavingsVault__factory } from '@mstable/protocol/types/generated/factories/BoostedSavingsVault__factory';

import { useSigner } from '../../../../context/OnboardProvider';
import { usePropose } from '../../../../context/TransactionsProvider';
import { useSelectedMassetState } from '../../../../context/DataProvider/DataProvider';

import { Interfaces } from '../../../../types';
import { TransactionManifest } from '../../../../web3/TransactionManifest';
import { MoreInfo } from '../../../core/Tabs';
import { SendButton } from '../../../forms/SendButton';

const formId = 'VaultExit';

const Container = styled.div`
  display: flex;
  flex-direction: column;
  gap: 2rem;
`;

export const VaultExit: FC = () => {
  const signer = useSigner();
  const propose = usePropose();

  const massetState = useSelectedMassetState();
  const savingsContract = massetState?.savingsContracts.v2;
  const vault = savingsContract?.boostedSavingsVault;
  const vaultAddress = vault?.address;
  const rawBalance = vault?.account?.rawBalance;

  const valid = !!rawBalance?.exact.gt(0);

  return (
    <Container>
      <SendButton
        valid={valid}
        title="Exit"
        handleSend={() => {
          if (vaultAddress && signer) {
            // TODO provide tranches
            propose<Interfaces.BoostedSavingsVault, 'exit()'>(
              new TransactionManifest(
                BoostedSavingsVault__factory.connect(vaultAddress, signer),
                'exit()',
                [],
                {
                  present: `Exiting the vault`,
                  past: `Exited the vault`,
                },
                formId,
              ),
            );
          }
        }}
      />
      <MoreInfo>
        {valid && 'This transaction will claim any available MTA rewards.'}
      </MoreInfo>
    </Container>
  );
};
